<template>
    <v-container>
        <v-theme-provider>
            <v-responsive>
                <section>
                    <v-card>
                        <v-container>
                            <v-img height="200px" :src="require('@/assets/4448.jpg')" light>
                                <v-card-title class='white--text mt-8'>
                                    <v-avatar size="56">
                                        <v-img alt='user' :src="require('@/assets/caja-de-votacion.png')"></v-img>
                                    </v-avatar>
                                    <p class="ml-3 black--text">Efectua Tu Voto</p>
                                </v-card-title>
                            </v-img>
                        </v-container>
                        <v-form lazy-validation ref='form' v-model="valid">
                            <v-card-text>
                                <v-row justify="center">
                                    <v-col cols="12" sm='6' md='6'>
                                        <v-autocomplete
                                            v-model="select"
                                            :loading="loading"
                                            :items="items"
                                            :search-input.sync="search"
                                            :rules="rules.required"
                                            cache-items
                                            label="Nombre del Nominado"
                                            placeholder='COVARRUBIAS CORTES ARMANDO ALBERTO'
                                            class="mx-4"
                                            item-text="nombre"
                                            item-value="id"
                                            flat
                                            hide-no-data
                                            hide-details
                                            solo-inverted
                                            outlined
                                            append-icon="mdi-magnify"
                                            @change="setListaSelect(select)"
                                        >
                                        </v-autocomplete>
                                    </v-col>
                                </v-row>
                            </v-card-text>
                            <v-card>
                                <v-data-table
                                    :loading='loading'
                                    loading-text="Espere un momento estamos cargando la informacion..."
                                    :headers="headers"
                                    :items="listTrabas"
                                    class="elevation-1"
                                >
                                    <template v-slot:[`item.actions`]="{ item }">
                                        <v-tooltip bottom>
                                            <template v-slot:activator="{ on: tooltip }">
                                                <v-btn
                                                    icon
                                                    color="red"
                                                    v-on="{ ...tooltip }"
                                                    @click="desactivar(item.id)"
                                                >   
                                                    <v-icon>
                                                        fas fa-trash
                                                    </v-icon>
                                                </v-btn>
                                            </template>
                                            <span>Desactivar Cliente</span>
                                        </v-tooltip>
                                    </template>
                                </v-data-table>
                                <v-card-actions v-if="listTrabas.length > 6">
                                    <v-btn color="primary" large @click="guardar"><v-icon class="mr-2">fas fa-save</v-icon> Guardar</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-form>
                    </v-card>
                </section>
            </v-responsive>
        </v-theme-provider>
    </v-container>
</template>
<script>
import { mapState } from 'vuex';
import { rules } from '../../globals/rules';
import { getName } from '../../login/helper/auth';
import { requestPost } from '../../globals/getAxios';

    export default {
        name:'votosEtica',
        data(){
            return{
                select:null,
                valid:true,
                search:null,
                rules:rules,
                loading: false,
                items:[],
                lista:[],
                trabajadores:[],
                headers:[
                    { text: 'ID', aling:'start', sortable:true, value:'id'},
                    {
                        text: 'Nombres',
                        sortable: true,
                        value:'nombre',
                    },
                    { text:'Puesto', sortable:true, value:'puesto', },
                    { text:'Acciones', value:'actions', sortable:false},
                ],
            }
        },
        watch:{
            search(val){
                val && val !== this.select && this.querySelector(val)
            },
        },
        created(){
            this.$store.dispatch('VotosModule/getTrabs')
        },
        computed:{
            ...mapState('VotosModule',['listTrabas'])
        },
        methods:{
            querySelector(v){
                this.loading = true
                this.overlay = true
                setTimeout(() => {
                    const asArray = Object.entries(this.$store.state.VotosModule.trabs);
                    let lista = asArray.filter(([, value]) => {
                        return ( value.nombre || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
                    })
                    lista.forEach(e => {
                        this.items.push(e[1]) 
                    })
                    this.loading = false
                    this.overlay = false
                }, 500)
            },
            setListaSelect(item){
                if (this.trabajadores.length < 7){
                    if (item.length !== 0){
                        if (getName() === this.items.find( e => item == e.id).nombre){
                            this.alerta("Atención", "No puede votar por si mismo", "warning")
                        }else{
                            if (this.trabajadores.find( e => item == e.id)){
                                this.alerta('Atención', "No Repita la seleccion", 'error')
                            }else{
                                this.trabajadores.push(this.items.find( e => item == e.id))
                                this.$store.commit('VotosModule/setListaTrabs', this.trabajadores)
                                this.select = ""
                            }
                            
                        }
                    }else{
                        this.alerta("Atención", "Debe Seleccionar un nombre", "warning")
                    }
                }else{
                    this.alerta("Atención", "Solo tiene 7 Votos", "warning")
                }
                
            },
            desactivar(item){
                this.trabajadores = this.trabajadores.filter( e => item !== e.id)
                this.$store.commit('VotosModule/deleteTrabs', this.trabajadores)
            },
            alerta(titulo, texto, icono){
                this.$swal({
                    title:titulo,
                    text:texto,
                    icon:icono,
                    footer:"<h3>Dirección de Informática</h3>"
                })
            },
            async guardar(){
                const salvar = await requestPost('/encuesta/setVotados', [this.listTrabas, getName()])
                if (salvar === 'exito'){
                    this.$store.commit('VotosModule/setListaTrabs', [])
                    this.alerta('Felicidadedes!!', 'Sus votos se guradaron con exito', 'success')
                    this.trbajadores = []
                }else if(salvar == 401){
                    this.alerta('Error!!', 'Usted ya realizo sus votos', 'error')
                    this.$store.commit('VotosModule/setListaTrabs', [])
                    this.trbajadores = []
                }
            },
        },
    }
</script>
